import React from "react";
import { NextResponse } from "next/server";
import path from "path";
import { promises as fs } from "fs";
import {
  Document,
  Page,
  Text,
  View,
  StyleSheet,
  Font,
  renderToStream,
} from "@react-pdf/renderer";
import {
  siteConfig,
  timelineData,
  projectsData,
  educationItems,
  certifications,
  nextSteps,
} from "@/lib/data";

let fontsRegistered = false;

async function ensureFonts() {
  if (fontsRegistered) return;
  const fontDir = path.join(process.cwd(), "public", "fonts");
  const regularFont = path.join(fontDir, "Inter-Regular.ttf");
  const semiBoldFont = path.join(fontDir, "Inter-SemiBold.ttf");

  await fs.access(regularFont);
  await fs.access(semiBoldFont);

  Font.register({ family: "Inter", src: regularFont });
  Font.register({ family: "Inter-Bold", src: semiBoldFont });
  fontsRegistered = true;
}

const styles = StyleSheet.create({
  page: {
    padding: 40,
    fontFamily: "Inter",
    fontSize: 11,
    lineHeight: 1.4,
    color: "#0f0f10",
  },
  name: {
    fontFamily: "Inter-Bold",
    fontSize: 24,
  },
  role: {
    fontSize: 14,
    marginTop: 4,
  },
  contact: {
    fontSize: 10,
    marginTop: 8,
  },
  section: {
    marginTop: 18,
  },
  sectionTitle: {
    fontFamily: "Inter-Bold",
    fontSize: 13,
    textTransform: "uppercase",
    marginBottom: 6,
  },
  paragraph: {
    marginBottom: 8,
  },
  listItem: {
    marginLeft: 8,
  },
});

const CvDocument: React.FC = () => (
  <Document title={`Currículo - ${siteConfig.name}`}>
    <Page size="A4" style={styles.page}>
      <View>
        <Text style={styles.name}>{siteConfig.name}</Text>
        <Text style={styles.role}>{siteConfig.title}</Text>
        <Text style={styles.contact}>
          {`${siteConfig.email} • ${siteConfig.phone} • ${siteConfig.location}`}
        </Text>
      </View>

      <View style={styles.section}>
        <Text style={styles.sectionTitle}>Resumo</Text>
        <Text style={styles.paragraph}>{siteConfig.subheadline}</Text>
      </View>

      <View style={styles.section}>
        <Text style={styles.sectionTitle}>Disponibilidade</Text>
        <Text>{siteConfig.availability}</Text>
      </View>

      <View style={styles.section}>
        <Text style={styles.sectionTitle}>Experiências Relevantes</Text>
        {timelineData.map((item) => (
          <View key={item.id} style={{ marginBottom: 10 }}>
            <Text style={{ fontFamily: "Inter-Bold" }}>
              {item.year} • {item.title} ({item.subtitle})
            </Text>
            <Text style={styles.paragraph}>{item.description}</Text>
            {item.achievements?.map((ach, index) => (
              <Text key={index} style={styles.listItem}>
                • {ach}
              </Text>
            ))}
          </View>
        ))}
      </View>

      <View style={styles.section}>
        <Text style={styles.sectionTitle}>Projetos em Destaque</Text>
        {projectsData
          .filter((project) => project.featured)
          .map((project) => (
            <View key={project.id} style={{ marginBottom: 10 }}>
              <Text style={{ fontFamily: "Inter-Bold" }}>{project.title}</Text>
              <Text>{project.description}</Text>
              <Text style={{ fontSize: 10, color: "#555", marginTop: 2 }}>
                Stack: {project.technologies.join(", ")}
              </Text>
            </View>
          ))}
      </View>

      <View style={styles.section}>
        <Text style={styles.sectionTitle}>Formação Acadêmica</Text>
        {educationItems.map((edu) => (
          <View key={edu.id} style={{ marginBottom: 10 }}>
            <Text style={{ fontFamily: "Inter-Bold" }}>
              {edu.course} — {edu.institution} ({edu.period})
            </Text>
            <Text>{edu.description}</Text>
          </View>
        ))}
      </View>

      <View style={styles.section}>
        <Text style={styles.sectionTitle}>Certificações</Text>
        {certifications.map((cert) => (
          <View key={cert.id} style={{ marginBottom: 10 }}>
            <Text style={{ fontFamily: "Inter-Bold" }}>
              {cert.year} • {cert.title} — {cert.institution}
            </Text>
            <Text>{cert.description}</Text>
          </View>
        ))}
      </View>

      <View style={styles.section}>
        <Text style={styles.sectionTitle}>Próximos Passos</Text>
        {nextSteps.map((step) => (
          <View key={step.id} style={{ marginBottom: 10 }}>
            <Text style={{ fontFamily: "Inter-Bold" }}>
              {step.title} [{step.status}]
            </Text>
            <Text>{step.description}</Text>
          </View>
        ))}
      </View>
    </Page>
  </Document>
);

async function streamToBuffer(stream: NodeJS.ReadableStream) {
  return new Promise<Buffer>((resolve, reject) => {
    const chunks: Uint8Array[] = [];
    stream.on("data", (chunk) => chunks.push(chunk));
    stream.on("error", reject);
    stream.on("end", () => resolve(Buffer.concat(chunks)));
  });
}

export async function GET() {
  await ensureFonts();
  const stream = await renderToStream(<CvDocument />);
  const pdfBuffer = await streamToBuffer(stream);

  return new NextResponse(pdfBuffer, {
    status: 200,
    headers: {
      "Content-Type": "application/pdf",
      "Content-Disposition": `attachment; filename="leandro-rocha-cv.pdf"`,
    },
  });
}
